@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600&family=Roboto:ital,wght@0,400;0,500;0,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600&family=Roboto:ital,wght@0,400;0,500;0,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600&family=Poppins:wght@500&family=Roboto:ital,wght@0,400;0,500;0,700;1,900&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
    // font-style: italic;
    // text-transform: uppercase;
    transition: all .2s linear;
    outline: none;
}
html{
    font-size: 62.5%;
    scroll-behavior: smooth;
}
body{
    overflow-x: hidden;
    width: 100%;
    height: 100%;
    background-color: #000;
}
  a{
    text-decoration: none;
    color: #fff;
  }
  
  h1,h2,h3,h4,h5,h6,span{
    font-size: 1.8rem;
    color: #fff;
    font-family: Poppins,sans-serif;
  }
  li{
    overflow: hidden;
  }
  span{
    display: block;
  }
  p{
    font-size: 1.6rem;
    font-family: Poppins,sans-serif;
  }
  .white{
    color: #fff!important;
  }
  .black{
    color: #000!important;
  }
  .d__flex{
    display: flex;
  }
  .align__items__center{
    align-items: center;
  }
  .justify__content__center{
    justify-content: center;
  }
  .justify__content__space__between{
    justify-content: space-between;
  }
  .mx__15{
    margin: 0px 1.5rem;
  }
  .px__30{
    padding: 0px 3rem;
  }
  .pxy__30{
    padding: 3rem;
  }
  .container{
    width: 92%;
    margin: 0px auto;
  }
  .pz__10{
    padding: 1rem 0;
  }
  .sweet{
    color: #F9004D;
  }
  .row{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    overflow: hidden;
    align-items: flex-start;
    justify-content: center;
  }
  .col__2 {
    max-width: 50%;
    display: flex;
    padding: 2rem;
    justify-content: center;
    flex-direction: column;
    @media screen and (max-width: 992px) {
      max-width: 100%;
    }
  }
  .col__3{
    max-width: 33.333333%;
    display: flex;
    padding: 2rem;
    justify-content: center;
    flex-direction: column;
    margin-top: 2rem;
  }
  .component__space{
    padding: 10rem 0;
  }
  .pointer{
    cursor: pointer;
  }
  svg {
    stroke-width: 1!important;
  }
  .relative{
    position: relative;
  }
  .absolute{
    position: absolute;
  }
  strong{
    color: #F9004D;
    font-weight: 600;
  }
  input{
    color: rgba(198,201,216,.75);
  }
  button.btn {
    font-size: 1.8rem;
    font-weight: 500;
    border-radius: .5rem;
    border: .2rem solid #f9004d;
    background-color: #f9004d;
    color: #fff!important;
    text-transform: uppercase;
    letter-spacing: .2rem;
    transition: .4s all ease;
    height: 5rem;
    width: 18rem;
    margin: 0 1rem;
  }
  .p__color {
    color: rgba(198,201,216,.75);
    font-size: 1.8rem;
    line-height: 3rem;
    font-family: Poppins,sans-serif;
    margin-bottom: 1.5rem;
  }
  button.about.btn.pointer:hover {
    background: #fff;
    color: #000!important;
  }
  /* RESPONSIVE */
  @media only screen and (max-width: 768px){
  .col__2 {
      max-width: 100%;
  }
  }
  @media only screen and (max-width: 768px){
  .col__3 {
      max-width: 50%;
  }
  }
  @media only screen and (max-width: 425px){
  .col__3 {
      max-width: 100%;
  }
  }
  @media only screen and (max-width: 425px){
  span.copyright {
      font-size: 1.3rem!important;
  }
  .footer.d__flex.align__items__center.justify__content__space__between.pz-10 {
    flex-wrap: wrap;
  }
  }
  

// NAVBAR,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,

.header{
    position: fixed;
    height: 9rem;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    // overflow: hidden;
  navbar{
    display: flex;
    height: 12vh;
    background-color: transparent;
    align-items: center;
    // z-index: 1;
    .logo {
        margin-left: 8rem;
        a{
            color: #fff;
            text-decoration: none;
            font-size: 2.4rem;
            font-weight: 700;
            i{
                font-size: 2.4rem;
                color: #fff;
                &:hover{
                    color: #fff;
                }
            }
        }
        @media screen and (max-width: 600px) {
          margin-left: 3rem;
        }
        @media screen and (max-width: 445px) {
          margin-left: 1rem;
        }
    }
    .icon{
        display: none;
        @media screen and (max-width: 968px) {
            display: block;
            color: #fff;
            font-size: 2.4rem;
            margin-left: auto;
            margin-right: 7rem;
            cursor: pointer;
        }
        @media screen and (max-width: 600px) {
          margin-right: 3rem;
        }
        @media screen and (max-width: 445px) {
          margin-right: 1rem;
        }
    }
    .nav-menu{
        display: flex;
        list-style-type: none;
        width: 70vw;
        justify-content: flex-start;
        padding-top: 1rem;
        // padding: 1rem;
        @media screen and (max-width:968px) {
            flex-direction: column;
            background-color: rgba(0,0,0,.9);
            width: 100%;
            position: absolute;
            top: 0rem;            
            text-align: center;
            height: 100vh;
            z-index: -1;
            padding-top: 8rem!important;
            left: -100%;
        }
        
        .nav-item{
          padding: 0 2rem;
            @media screen and (max-width: 968px) {
               padding: 2rem 0;
               margin-left: -2rem;
               &:hover{
                 background-color: #191919;
               }
              }
        }
        .nav-item a{
            text-decoration: none;
            color: #fff;
            font-size: 1.6rem;
            &:hover{
              color: #f9004d; 
                @media screen and (max-width: 968px) {
                  border: none;
                }        
            }
            @media screen and (max-width: 968px) {
              font-size: 2.5rem;
             }
        }
    }
    .nav-menu.active{
        @media screen and (max-width: 968px) {
            left: 0;
            transition: all .5s ease-in-out;
        }
    }
    .navbar.colorChange{
      background-color: #191919;
    }
  }
}
.header-bg{
  background-color: #191919;
}


// HOME.....................................................................
.home{
  .home__bg{
    background-image: url(./component/images/bg.jpg);
    background-color: #010101;
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    // margin-top: -8rem;
    height: 100vh;
    // .nav__items{
    //   font-size: 1.8rem;
    //   padding-top: 1.5rem;
    //   transition: 0.4 ease-in-out;
    // }
    .home__content{
      padding: 12.5rem 0;
      .home__meta{
        padding-top: 12rem;
        h1{
          font-size: 1.8rem;
          font-weight: 400;
          letter-spacing: .3rem;
          color: rgba(198,201,216,.75)!important;
          @media screen and (max-width: 600px) {
            font-size: 1.6rem;
          }
          @media screen and (max-width: 445px) {
            font-size: 1.4rem;
          }
        }
        h2{
          font-size: 5.4rem;
          line-height: 5.4rem;
          font-weight: 800;
          @media screen and (max-width: 600px) {
            font-size: 5rem;
          }
          @media screen and (max-width: 445px) {
            font-size: 4rem;
          }
        }
        h3{
          font-size: 5rem;
          font-weight: 800;
          line-height: 4rem;
          @media screen and (max-width: 600px) {
            font-size: 4.5rem;
          }
          @media screen and (max-width: 445px) {
            font-size: 3.5rem;
          }
        }
        h4{
          font-size: 5rem;
          font-weight: 800;
          line-height: 4rem;
          @media screen and (max-width: 600px) {
            font-size: 4.5rem;
          }
          @media screen and (max-width: 445px) {
            font-size: 3.5rem;
          }
        }
        @media screen and (max-width: 768px) {
          text-align: center;
        }
      }
    }
  }
}

// About ..............................

.about{
  background: #191919;
  .about__img{
    border-radius: 1rem;
    width: 100%;
  }
  .about__heading{
    margin-bottom: 1rem;
    font-size: 6rem;
    font-weight: 700;
    text-align: start;
    font-family: 'Mulish', sans-serif;
    @media screen and (max-width: 992px) {
      text-align: center;
    }
  }
  .about__meta{
    .about__button{
          padding: 2rem 0;
      .pointer{
        text-transform: capitalize;
        font-size: 1.8rem;
        width: 19.5rem;
        height: 5.5rem;
        transition: .4s ease;
        @media screen and (max-width: 580px) {
          margin: 1rem 0;
        }
      }
      @media screen and (max-width: 992px) {
        justify-content: center;
      }
      @media screen and (max-width: 580px) {
        flex-direction: column;
      }
    }
    @media screen and (max-width: 1024px) {
      p{
        font-size: 1.5rem;
      }
    }
    @media screen and (max-width: 992px) {
      p{
        text-align: center;
      }
    }
    @media screen and (max-width: 500px) {
      p{
        font-size: 1.5rem;
      }
    }
  }
  .up__to__top__btn{
    height: 4rem;
    width: 4rem;
    position: fixed;
    bottom: 3%;
    right: 2%;
    background-color: #F9004D;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10000;
    i{
      padding-top: 1.3rem;
      padding-left: 1.3rem;
      font-size: 1.4rem;
    }
  }
}
  
// Service.......................................


.service{
  .heading{
    h1{
      font-size: 6rem;
      text-align: center;
      font-weight: 700;
      margin-bottom: 1rem;
      font-family: Popins, sans-serif;
      @media screen and (max-width: 992px) {
        font-size: 4.5rem;
      }
      @media screen and (max-width: 768px) {
        font-size: 3.5rem;
      }
      @media screen and (max-width: 400px) {
        font-size: 3rem;
      }
      @media screen and (max-width: 350px) {
        font-size: 2.5rem;
      }
    }
    p{
      margin-bottom: 0;
      text-align: center;
      line-height: 1.2;
      @media screen and (max-width: 768px) {
        font-size: 1.7rem;
      }
      @media screen and (max-width: 768px) {
        padding: 0 .5rem;
      }
      @media screen and (max-width: 350px) {
        font-size: 1.3rem;
      }
    }
  }
  .service__box{
    // z-index: -1;
    border-radius: 1rem;
    width: 100%;
    background-color: #191919;
    padding: 5.5rem;
    transition: .4s ease;
    @media screen and (max-width: 1400px) {
      padding: 3.5rem;
    }
    @media screen and (max-width: 1200px) {
      padding: 4rem 5rem;
      text-align: center;
    }
    @media screen and (max-width: 992px) {
      padding: 4rem 2rem;
    }
    @media screen and (max-width: 445px) {
      text-align: center;
      padding: 5rem 2rem;
    }
    .icon{
        font-size: 5.4rem;
        font-weight: 400;
        margin-bottom: 2.3rem;
        display: inline-flex;
        color: #f9004d;
    }
    h1{
      font-size: 2rem;
      font-family: Poppins,sans-serif;
      padding: 1rem 0;
      @media screen and (max-width:768px) {
        font-size: 1.7rem;
      }
    }
    p{
      margin-bottom: 0;
      color: rgba(198,201,216,.75);
      @media screen and (max-width:768px) {
        font-size: 1.6rem;
      }
    }
    &:hover{
      // z-index: -1;
      transform: translateY(.5rem);
      background-image: linear-gradient(90deg, #f61b10, #ef0963);
      .icon{
        color: #fff;
      }
      p{
        color: #fff;
      }
    }
  }
  @media screen and (max-width: 992px) {
    .col__3{
      max-width: 50%;
    }
  }
  @media screen and (max-width: 768px) {
    .col__3{
      max-width: 100%;
    }
  }
}


// PROJECT ................................................................................

.project{
  background-color: #191919;
  .heading{
    h1{
      font-size: 6rem;
      text-align: center;
      font-weight: 700;
      margin-bottom: 1rem;
      font-family: Popins, sans-serif;
      @media screen and (max-width: 992px) {
        font-size: 5rem;
      }
      @media screen and (max-width: 768px) {
        font-size: 4rem;
      }
      @media screen and (max-width: 368px) {
        font-size: 3rem;
      }
    }
    p{
      margin-bottom: 0;
      text-align: center;
      line-height: 1.2;
      color: rgba(198,201,216,.75);
      @media screen and (max-width: 992px) {
        font-size: 1.4rem;
      }
      @media screen and (max-width: 768px) {
        font-size: 1.3rem;
      }
      @media screen and (max-width: 368px) {
        padding: 0 1rem;
      }
    }
  }
  .project__box{
    .project__box__img{
      .project__img__box{
        img{
          border-radius: 1rem;
          width: 100%;
          object-fit: cover;
          transition: .4s;
          opacity: .7;
          position: relative;
        }
        .project__img{
          &::after{
            content: "";
            width: 100%;
            height: 100%;
            background-color: #000;
            z-index: 1;
            opacity: 1;
          }
        }
      }
      .mask__effect{
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: #f9004d;
        border-radius: 1rem;
        transition: .4s;
        opacity: .3;
        position: absolute;
        display: none;
      }
    }
    &:hover{
      img{
        transform: scale(1.1);
      }
      .mask__effect{
        display: inline;
        transform: scale(1.1);
      }
    }
    .project__meta{
      top: 0%;
      left: 0%;
      display: flex;
      height: 100%;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      flex-wrap: wrap;
      border-radius: 1rem;
      width: 100%;
      h5{
        font-weight: 400;
        font-size: 2rem;
        padding-bottom: 2rem;
        @media screen and (max-width: 1200px) {
          font-size: 1.7rem;
        }
        @media screen and (max-width: 992px) {
          font-size: 1.9rem;
        }
        @media screen and (max-width: 768px) {
          font-size: 2.2rem;
        }
        @media screen and (max-width: 450px) {
          font-size: 2rem;
        }
      }
      h4{
        font-size: 2.2rem;
        font-family: Poppins, sans-serif;
        font-weight: 700;
        padding-bottom: 2rem!important;
        @media screen and (max-width: 1400px) {
          text-align: center;
        }
        @media screen and (max-width: 1200px) {
          font-size: 1.9rem;
        }
        @media screen and (max-width: 992px) {
          font-size: 2.2rem;
        }
        @media screen and (max-width: 768px) {
          font-size: 2.5rem;
        }
        @media screen and (max-width: 550px) {
          padding: 0rem 1rem;
          font-weight: 500;
        }
        @media screen and (max-width: 450px) {
          font-size: 2.2rem;
        }
      }
      a{
        padding: 1.2rem 3.5rem;
        border: .2rem solid #ffffff87;
        border-radius: .5rem;
        transition: .4s ease;
        font-size: 1.4rem;
        &:hover{
          background-color: #f9004d;
          border: .2rem solid #f9004d;
          color: #fff;
        }
        @media screen and (max-width: 1200px) {
          font-size: 1rem;
        }
        @media screen and (max-width: 992px) {
          font-size: 1.3rem;
        }
        @media screen and (max-width: 768px) {
          font-size: 1.5rem;
        }
        @media screen and (max-width: 450px) {
          font-size: 1.3rem;
        }
      }
    }
  }
  @media screen and (max-width: 992px) {
    .col__3{
      max-width: 50%;
    }
  }
  @media screen and (max-width: 768px) {
    .col__3{
      max-width: 100%;
    }
  }
}
  
  
