  }
  @media screen and (max-width: 768px) {
    .col__3{
      max-width: 100%;
    }
  }
}


// CONTACT..................................................


.contact{
  background-color: #191919;
  .contact__box{
    .contact__meta{
      h1{
        font-size: 6rem;
        @media screen and (max-width: 330px) {
          font-size: 4rem;
        }
      }
      p{
        font-size: 1.8rem;
        line-height: 3rem;
        color: rgba(29,29,36,.75);
        font-weight: 400;
        @media screen and (max-width: 330px) {
          font-size: 1.4rem;
        }
      }
    }
    .input__box{
      input{
        display: block;
        width: 100%;
        padding: 0 2rem;
        border: .2rem solid rgba(0,0,0,.1);
        border-radius: .5rem;
        transition: all .3s ease;
        height: 5rem;
        line-height: 4.6rem;
        margin: 2rem 0;
        outline: none;
        color: #fff;
        font-size: 1.5rem;
        letter-spacing: .1rem;
        background: none;
        border: .2rem solid hsla(0,0%,100%,.2);
      }
      textarea{
        height: 12rem;
        background: none;
        padding: 0 2rem;
        width: 100%;
        line-height: 4.6rem;
        color: #fff;
        font-size: 1.5rem;
        letter-spacing: .1rem;
        border: .2rem solid hsla(0,0%,100%,.2);
      }
      .btn{
        margin: 2rem 0;
        @media screen and (max-width: 768px) {
          width: 100%;
        }
      }
    }
  }
  img{
    border-radius: 1rem;
    width: 100%;
    overflow: hidden;
  }
  @media screen and (max-width: 1400px) {
    .col__2{
      max-width: 100%;
    }
  }
}



// FOOTER............................................

